.hero-img{
    width: 100%;
    background: rgba(0,0,0,0.5 );
    height: 60vh;
    position: relative;
}

.hero-img::before{
    content: "";
    background: url("https://images.unsplash.com/photo-1584949091598-c31daaaa4aa9?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxzZWFyY2h8MTgxfHxwcm9ncmFtbWluZ3xlbnwwfHwwfHw%3D&auto=format&fit=crop&w=800&q=60");
    background-size: cover;
    background-position: center top;
    height: 100%;
    width: 100%;
    position:absolute;
    top:0;
    left:0;
    z-index: -1;
}
.resume-pic{
    height: 30%;
    width:90%;
    margin-top: 300px;
}
.heading{
    width:100%;
    height:100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
    z-index: 1;
}

.hero-img h1{
    position: absolute;
    font: 10rem;
    top: 35%;
    left: 50%;
    transform: translate(-50%, -50%);
    margin: 0;
    color: white;
}

.hero-img p {
    font-size: 1.4;
    text-align: center;
}

@media screen and (max-width:640px){
    .hero-img h1 {
        font-size: 2rem;
    }
    
}